/* 


--- TYPOGRAPHY SYSTEM


- Font weights

- Line heights
Default: 1

--- COLORS

Primary: #51cf66 #ffb300;
Tints: #40c057 
Shades:
Accents: 
Grays: #868e96
Background color: #717171 #fff


*/

/* UNIVERSAL */

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Rubik", sans-serif;
  line-height: 1;
  font-weight: 400;
  color: #fff;
  background-color: #fff;
  min-height: 100vh;
}

/* GENERAL */

.hidden {
  display: none !important;
}

.title {
  color: rgb(0, 0, 0);
  font-size: 24px;
  font-weight: 600;
}

.transparent-black-screen {
  position: absolute;
  top: 0%;
  left: 0%;
  color: black;
  background-color: rgba(0, 0, 0, 0.3);
  width: 100%;
  height: 100vh;
  border: none;
  z-index: -1;
}

.flex-column {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.container {
  height: auto;
  width: 80rem;
  border-radius: 8%;
  text-align: center;
  justify-content: center;
  color: rgb(0, 0, 0);
  background-color: #ffffff;
  margin: 15rem 5rem 5rem 5rem;
  border: 0.3rem solid #ffb300;
  box-shadow: 0 0 0.5rem #0000003d;
}

.submit-btn {
  width: auto;
  border: none;
  color: #fff;
  padding: 0.8rem;
  margin: 3rem 38% 3rem 38%;
  font-weight: 450;
  font-size: 1.8rem;
  border-radius: 10px;
  transition: all 0.2s;
  text-decoration: none;
  background-color: #40c057;
}

.submit-btn:hover,
.submit-btn:active {
  transform: scale(1.1);
  transition: all 0.2s;
}

.grid {
  display: grid;
  row-gap: 2.4rem;
  grid-template-columns: repeat(1, 1fr);
}

.grid--2-columns {
  grid-template-columns: repeat(2, 1fr);
}

.grid--3-columns {
  grid-template-columns: repeat(3, 1fr);
}

.grid--4-columns {
  grid-template-columns: repeat(4, 1fr);
}

.grid--5-columns {
  grid-template-columns: repeat(5, 1fr);
}
